body {
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: rgb(21, 173, 173);
  text-align: center;
}

h1 {
  padding: 10px;
  font-size: 40px;
  color: aliceblue;
  font-weight: bold;
}

h2 {
  font-weight: bold;
  color: aliceblue;
  text-align: center;
}

h3 {
  color: black;
}

nav {
  position: fixed;
  top: 40px;
  width: 150px;
  height: 150px;
  background-color: aliceblue;
  padding: 5px;
  margin: 15px;
  border: 3px solid rgb(223, 219, 31);
}

ul {
  list-style-type: none;
  width: 60%;
}

li {
  margin: 2px;
}

li:hover {
  background-color: rgb(50, 218, 218);
}

a {
  font-weight: bold;
  padding: 10px;
  padding-right: 10px;
}

footer {
  margin: 30px;
  padding: 20px;
  text-align: center;
  font-size: 20px;
}

.hello {
  display: inline-block;
}

.header {
    display: inline-block;
    width: 40%;
    padding: 5px;
    font-family: 'Courier New', Courier, monospace;
    font-size: 40px;
}


.memTable {
    display: inline-block;
    table-layout: fixed;
    width: 90%;
    background-color: whitesmoke;
    padding: 20px;
    border: solid 2px black;
    border-collapse: collapse;
  }

.actTable {
  table-layout: fixed;
  background-color: whitesmoke;
  padding: 20px;
  border: solid 3px black;
  border-collapse: collapse;
}

  td {
    padding-left: 50px;
    padding-right: 50px;
    font-size: 18px;
    border: solid 2px rgb(14, 190, 235);
  }

  th {
    font-size: 20px;
    border: solid 2px rgb(14, 190, 235);
  }

  thead tr{
    color: black;
    font-weight: bold;
  }

.menu {
  margin-top: 10px;
  margin-left: 20px;
}

.home {
  text-align: center;
}

.members {
  display: inline-block;
  margin: 0;
  text-align: center;
  width: 1000px;
}

.activities {
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.content {
  display: inline-block;
  background-color: rgb(82, 193, 221);
  padding: 20px;
  margin: 40px;
  border: 3px solid black;
  width: 640px;
  height: 400px;
  text-align: center;
}

.aboutContent {
  display: inline-block;
  background-color: rgb(82, 193, 221);
  padding: 20px;
  margin: 20px;
  border: 3px solid black;
  width: 800px;
  height: 600px;
  text-align: center;
}

.loginHome {
  text-align: center;
}

.loginContent {
  display: inline-block;
  background-color: #6A5ACD;
  padding: 20px;
  margin: 40px;
  border: 3px solid black;
  width: 640px;
  height: 280px;
  text-align: center
}

.activitiesContent {
  display: inline-block;
  background-color: rgb(82, 193, 221);
  padding: 10px;
  margin: 15px;
  border: 3px solid black;
  width: 440px;
  height: 140px;
  text-align: center
}

button, input {
  margin: 10px;
  padding: 5px;
  font-weight: bold;
}

.reactBanner {
  width : 890px;
  height: 160px;
  border: 3px solid rgb(15, 126, 170);
}

.codeCart {
  width: 400px; 
  height: 400px;
  margin: 10px;
  border: 2px solid rgb(179, 176, 12);
}